.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img{
  max-width:100px;
}
header img{
  position: absolute;
  top: 25px;
  right: 20px;
  max-width:50px;
}
p#trolli{
  font-size:1px;
}
div#ostoskori{
  border:solid 1px black;
  width:20%;
  position:absolute;
  right:2px;
  top:97px;
  border-radius:4px;
}
form#form{
  position:absolute;
  left:40%;
  top: 25%;
}
div#modal{
  background-color: gray;
  width: 100vw;
  height: 50.4vw;
  position:absolute;
  top: 0;
  opacity: 50%;
}
div#insidemodal{
  background-color:white;
  position:relative;
  bottom: 20vw;
  max-width:50%;
  left: 25%; 
}
div#cartwrapper{
  width:100%;
  display:flex;
  flex-direction:row;
  justify-content: center;
}
p#counter{
  border: black 1px solid;
  width: 15px;
  margin-left:43%;
}